body {
  margin: 0;
  font-family: Inter;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f5f8f9;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

#root {
  min-height: 100%;
  overflow: hidden;
  position: relative;

  display: flex;
  flex-direction: column;
  padding: 0px 24px;
  background: center / cover no-repeat url("./img/svgicons/bg.svg");
}
p {
  font-size: 12px;
}
.content {
  display: flex;
  flex-direction: column;
  position: relative;
  flex: 1 1 auto;
  padding: 84px 20px 90px;
  z-index: 2;
  max-width: 365px;
  width: 100%;
  margin: 0 auto;
  // max-height: calc(100vh - 50px);
  // overflow-y: auto;
  // overflow-x: hidden;
}
.btn {
  display: flex;
  gap: 10px;
  font-weight: 700;
  font-size: 10px;
  text-align: center;
  align-items: center;
  justify-content: center;
  padding: 8px 12px;
  border-radius: 16px;
  min-width: fit-content;

  &.with-img {
    margin-top: 20px;
    padding: 12px;
    justify-content: flex-start;
    h3 {
      font-weight: 700;
      font-size: 14px;
      text-align: left;
      margin-bottom: 4px;
    }
    p {
      font-weight: 700;
      font-size: 10px;
      text-align: left;
    }
  }
}
.btn-bordered {
  padding: 2px;
  border-radius: 16px;
  & p {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #e9efff;
    font-weight: 700;
    font-size: 16px;
    text-align: center;
    color: #3f2aa9;
    border-radius: 16px;
    padding: 8px 12px;
  }
}

.btn-gradient {
  color: #fff;
  background: linear-gradient(360deg, #ff007a 0%, #9c00c3 100%);

  & .text p {
    color: #fff;
  }
}
.btn-blue {
  width: 100%;
  background: linear-gradient(360deg, #0085cc 0%, #00a6ff 100%);
  & .text p {
    color: #fff;
  }
}
.block-white {
  background: #e9efff;
  border-radius: 8px;
  padding: 20px;

  & p {
    font-weight: 400;
    font-size: 14px;
    color: #1a2b63;
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
}
.block-with-bg {
  background: #fff;
  border-radius: 8px;
  padding: 12px;
  margin-bottom: 20px;
}
.count-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  padding: 8px;
  background: #3f2aa9;
  color: #fff;
  font-weight: 600;
  font-size: 20px;
  text-align: center;
  margin-bottom: 12px;

  & p {
    color: #fff;
    margin-bottom: 0px !important;

    &:first-child {
      font-weight: 600;
      font-size: 20px;
      text-align: center;
    }
    &:last-child {
      font-weight: 600;
      font-size: 12px;
      text-align: center;
    }
  }
}
.tip {
  display: flex;
  gap: 12px;
  align-items: center;

  & p {
    font-size: 10px;
  }
}
.btn-ad {
  display: flex;
  position: relative;
  align-items: stretch;
  width: 100%;
  opacity: 0.5;

  &__indicator {
    border-radius: 8px 0 0 8px;
    background: #fff;
    width: 64px;
  }
  &__text {
    padding: 24px 16px;
    background: linear-gradient(0deg, #1a0b4e 0%, #4630ba 100%);
    border-radius: 0px 8px 8px 0px;
    flex: 1 1 auto;
  }
  &__ad {
    position: absolute;
    border-radius: 12px;
    padding: 0px 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: 14px;
    text-align: center;
    color: #1a2b63;
    top: -5px;
    right: 28px;
    background: #fff;
    text-transform: uppercase;
  }
  h3 {
    font-weight: 700;
    font-size: 14px;
    text-align: left;
    color: #fff;
  }
  p {
    font-weight: 500;
    font-size: 10px;
    text-align: left;
    margin-bottom: 0px;
    color: #fff;
  }
}
