@keyframes adShow {
    100% {
        box-shadow: 0 0 0 0 white;
        position: absolute;
        left: 0;
        top: 0;
        opacity: 1;
        transition: 0s;
    }
}
.task {
    &:not(:last-child) {
        margin-bottom: 20px;
    }
    &__header {
        h2 {
            font-weight: 700;
            font-size: 14px;
            color: #1a2b63;
            margin-bottom: 8px;
        }
        p {
            font-weight: 400;
            font-size: 12px;
            color: #1a2b63;
            margin-bottom: 8px;
        }
    }
    &__task {
        display: flex;
        gap: 10px;
        border-radius: 8px;
        padding: 8px 12px;

        position: relative;
        &.blue {
            background: linear-gradient(360deg, #0085cc 0%, #00a6ff 100%);
        }
        &.purple {
            background: linear-gradient(0deg, #1a0b4e 0%, #4630ba 100%);
        }
        &.green {
            background: linear-gradient(360deg, #00900e 0%, #04cc00 100%);
        }

        & img {
            width: 60px;
            height: 60px;
        }
        h3 {
            font-weight: 700;
            font-size: 14px;
            color: #fff;
            margin-bottom: 2px;
        }
        p {
            font-weight: 500;
            color: #fff;
            font-size: 10px;
        }
        &:not(:last-child) {
            margin-bottom: 8px;
        }

        &:has(.task__ad) {
            &::after {
                content: "";
                display: block;
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                opacity: 0;
                transition: all 0.5s;
                box-shadow: 0 0 10px 40px #0085cc;
                animation: adShow 600ms ease-in;
            }
        }
    }
    &__ad {
        position: absolute;
        border-radius: 12px;
        padding: 0px 12px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 500;
        font-size: 14px;
        text-align: center;
        color: #1a2b63;
        top: -5px;
        right: 28px;
        background: #fff;
    }
    &__new {
        position: absolute;
        border-radius: 8px;
        width: 8px;
        height: 8px;
        right: 6px;
        top: 6px;
        background: #fe3745;
    }
    &__done {
        filter: grayscale(0.8);
        cursor: not-allowed;
    }
    &__progress {
        margin-top: 10px;
        text-align: right;
        font-size: 8px !important;
    }
}
.partner {
    margin-top: 20px;
    & p,
    h3,
    h4 {
        color: #fff;
    }
    h3 {
        font-weight: 700;
        font-size: 10px;
    }
    h4 {
        font-weight: 500;
        font-size: 6px;
    }
    &__header {
        margin-bottom: 8px;
        h2 {
            font-weight: 700;
            font-size: 14px;
            color: #1a2b63;
        }
    }
    &__partner {
        display: block;
        &:not(:last-child) {
            margin-bottom: 8px;
        }
        border-radius: 8px;
        padding: 8px;
        background: linear-gradient(0deg, #1a0b4e 0%, #4630ba 100%);
    }
    &__partner-header {
        display: flex;
        justify-content: space-between;
        margin-bottom: 8px;
    }
    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 4px;
        padding: 4px 8px;
        background: #fff;
        width: 90px;
        height: 32px;
        margin-bottom: 8px;
        & img {
            object-fit: cover;
            height: 100%;
            width: 100%;
        }
    }
    &__text {
        p {
            font-weight: 400;
            font-size: 6px;
        }
    }
    &__clicks {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 8px;

        & p {
            font-weight: 700;
            font-size: 10px;
        }
    }
    &__coins {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        gap: 2px;
    }

    &__footer {
        font-weight: 400;
        font-size: 6px;
        text-align: center;
        color: #1a2b63;
        margin-top: 8px;
    }
}
.sponsor {
    & p {
        font-size: 10px;
    }

    .btn {
        border-radius: 8px;
        padding: 22px 12px;
        font-weight: 700;
        font-size: 16px;
        text-transform: uppercase;
        text-align: center;
        width: 100%;
        margin-bottom: 12px;
    }

    .btn-bordered {
        width: 100%;
        border-radius: 8px;
        margin-bottom: 12px;
        & p {
            border-radius: 6px;
            font-weight: 700;
            font-size: 16px;
            text-align: center;
            color: #3f2aa9;
            padding: 22px 12px;
        }
    }
    & p {
        color: #000;
    }
}
.sponsor-tabs {
    .tabs__links {
        grid-template-columns: repeat(3, 1fr);

        button p {
            font-size: 10px;
        }

        &.pink {
            button {
                p {
                    padding: 4px 12px;
                }
            }
        }
    }
}
.sponsor-block {
    display: flex;
    gap: 10px;
    align-items: center;
    margin-bottom: 20px;

    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        flex: 0 0 100px;
        height: 48px;
        padding: 4px;
        border-radius: 8px;
        background: #fff;
        & img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
    p {
        font-weight: 700;
        font-size: 12px;
        color: #1a2b63;
    }
}
.sponsor-bg {
    width: 100%;
    height: 150px;
    border-radius: 8px;
    overflow: hidden;
    margin-bottom: 20px;

    & img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
.sponsor-sale {
    font-weight: 600;
    font-size: 20px;
    text-align: center;
    color: #3f2aa9;
}
.sponsor-statement {
    display: flex;
    gap: 8px;
    align-items: center;
    justify-content: center;
    margin: 20px 0px;

    & p {
        font-weight: 700;
        font-size: 20px;
        color: #1a2b63;
    }
}
.sponsor-task {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    font-weight: 500;
    font-size: 10px;
    position: relative;

    &::before {
        content: "";
        display: block;
        border-radius: 8px;
        width: 8px;
        height: 8px;
    }
    &.red {
        color: #fe3745;
        &::before {
            background: #fe3745;
        }
    }
    &.green {
        color: rgb(33, 173, 108);
        &::before {
            background: #37fe69;
        }
    }
}
