.mobile-only {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    flex: 1 1 auto;

    .header__row {
        max-width: 400px;
    }
    h1 {
        margin-top: auto;
        margin-bottom: auto;
        align-self: center;
        font-size: 20px;
        background: #fff;
        border-radius: 20px;
        padding: 60px 30px;
        display: flex;
        flex-direction: column;
        max-width: fit-content;
        text-align: center;
    }
}
