.rocket-container {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    width: 100%;
    height: auto;
    aspect-ratio: 1/1;
    background: linear-gradient(0deg, #1a0b4e 0%, #4630ba 100%);
    margin: 8px 0px;
    overflow: hidden;
    position: relative;
}
.rocket {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    position: relative;
    z-index: 2;
    height: 80%;
    animation: rocketDiagonalSway 3s ease-in-out infinite;
    transition: all 300ms ease;

    &.turbo {
        scale: 0.8;
        animation: rocketDiagonalSwayTurbo 1.5s ease-in-out infinite;
        & .rocket-fire {
            -webkit-filter: hue-rotate(290deg);
            filter: hue-rotate(290deg);
            animation: flameFlicker 0.1s ease-in-out infinite;
        }
    }
}
.rocket-body {
    height: 80%;
}

.rocket-fire {
    position: relative;
    animation: flameFlicker 1s ease-in-out infinite;
    height: 20%;
}
@keyframes flameFlicker {
    0% {
        scale: 1;
        opacity: 1;
    }
    50% {
        scale: 1.05; /* Увеличение масштаба */
        opacity: 0.9; /* Уменьшение прозрачности */
    }
    100% {
        scale: 1;
        opacity: 1;
    }
}
@keyframes rocketDiagonalSway {
    0% {
        transform: translateX(0%) translateY(0);
    }
    25% {
        transform: translateX(0%) translateX(-5px) translateY(-3px);
    } /* Влево-вверх */
    50% {
        transform: translateX(0%) translateX(5px) translateY(-3px);
    } /* Вправо-вверх */
    75% {
        transform: translateX(0%) translateX(5px) translateY(-1px);
    }
    100% {
        transform: translateX(0%) translateY(0);
    }
}
@keyframes rocketDiagonalSwayTurbo {
    0% {
        transform: translateX(0%) translateY(0);
    }
    25% {
        transform: translateX(0%) translateX(-15px) translateY(-13px);
    } /* Влево-вверх */
    50% {
        transform: translateX(0%) translateX(15px) translateY(-13px);
    } /* Вправо-вверх */
    75% {
        transform: translateX(0%) translateX(15px) translateY(-11px);
    }
    100% {
        transform: translateX(0%) translateY(0);
    }
}
.plus-ones {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 3;
}

.plus-one {
    position: absolute;
    font-size: 2em;
    color: #fff;
    pointer-events: none;
    animation: fadeOutUp 1s forwards;
}

@keyframes fadeOutUp {
    0% {
        opacity: 1;
        transform: translateY(0);
    }
    100% {
        opacity: 0;
        transform: translateY(-50px);
    }
}

.stars {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.star {
    position: absolute;
    background-color: white;
    border-radius: 50%;
}
