.wallet-header {
    display: flex;
    border-radius: 8px;
    padding: 8px 12px;
    gap: 12px;
    align-items: center;
    justify-content: center;
    background: #fff;
    margin-bottom: 12px;

    & img {
        height: 32px;
        width: 32px;
        flex: 0 0 32px;
        aspect-ratio: 1/1;
    }
}
.listing-clicks {
    &__item {
        display: flex;
        gap: 8px;
        align-items: center;

        &:not(:last-child) {
            margin-bottom: 10px;
        }

        &.blue .listing-clicks {
            &__img {
                background: #1a2b63;
            }
            &__info p {
                font-weight: 700;
                font-size: 10px;
                background: linear-gradient(360deg, #0085cc 0%, #00a6ff 100%);
                background-clip: text;
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                margin-bottom: 0px;
            }
        }
        &.pink .listing-clicks {
            &__img {
                background: linear-gradient(360deg, #9c00c3 0%, #ff007a 100%);
            }
            &__info p {
                font-weight: 700;
                font-size: 10px;
                background: linear-gradient(360deg, #9c00c3 0%, #ff007a 100%);
                background-clip: text;
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                margin-bottom: 0px;
            }
        }
    }
    &__info {
        h3 {
            font-weight: 700;
            font-size: 10px;
            color: #1a2b63;
        }
    }
    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 28px;
        height: 28px;
        aspect-ratio: 1/1;
        flex: 0 0 28px;
        border-radius: 50%;

        & img {
            height: 18px;
            width: 12px;
        }
    }
}
.listing-stats {
    margin-bottom: 20px;
    &__img {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 28px;
        height: 28px;
        aspect-ratio: 1/1;
        flex: 0 0 28px;
        border-radius: 50%;
        background: linear-gradient(360deg, #9c00c3 0%, #ff007a 100%);

        & img {
            height: 18px;
            width: 12px;
        }
    }
    &__item {
        opacity: 0.5;
        display: flex;
        gap: 10px;
        align-items: center;
        border-radius: 8px;
        padding: 12px;
        background: #fff;
        h4 {
            font-weight: 400;
            font-size: 8px;
            color: #596064;
            margin-bottom: 2px;
        }
        h3 {
            font-weight: 700;
            font-size: 10px;
            color: #1a2b63;
            margin-bottom: 2px;
        }
        p {
            font-weight: 700;
            font-size: 12px;
            color: #1a2b63;
        }

        &.blue {
            opacity: 1;
            .listing-stats {
                &__img {
                    background: linear-gradient(360deg, #0085cc 0%, #00a6ff 100%);
                }
            }
            p {
                color: transparent;
                font-weight: 700;
                font-size: 12px;
                background: linear-gradient(360deg, #0085cc 0%, #00a6ff 100%);
                background-clip: text;
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
        }
    }
}
.listing-invite {
    &__header {
        display: flex;
        gap: 10px;
        align-items: center;
        margin-bottom: 20px;

        h2 {
            font-weight: 700;
            font-size: 14px;
            text-transform: uppercase;
            color: #1a2b63;
        }
    }
    .btn {
        border-radius: 8px;
        width: 100%;
        padding: 22px 15px;
        font-size: 16px;
        text-transform: uppercase;
    }
}
