.header {
    position: fixed;
    top: 20px;
    left: 20px;
    right: 20px;
    height: 56px;
    z-index: 10;
    &__row {
        display: flex;
        gap: 20px;
        align-items: center;
        max-width: fit-content;
        border-radius: 8px;
        padding: 8px 20px;
        background: #fff;
        margin: 0 auto;
    }
    &__logo {
        display: flex;
        gap: 9px;
        align-items: center;
        max-width: 100px;
        & img {
            flex: 0 0 38px;
            aspect-ratio: 1/1;
        }

        & p {
            font-weight: 700;
            font-size: 12px;
            color: #1a2b63;
        }
    }
    &__profile {
        display: flex;
        gap: 8px;
        align-items: center;
        padding-left: 20px;

        & p {
            font-weight: 400;
            font-size: 12px;
            color: #1a2b63;
        }
        & img {
            flex: 0 0 31px;
            aspect-ratio: 1/1;
        }
    }
    &__lang {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 4px;
        padding: 4px;
        font-weight: 400;
        font-size: 10px;
        text-align: right;
        color: #fff;
        text-transform: uppercase;
        background: #3f2aa9;
    }
}
