.wheel {
    margin-top: 16px;
    h2 {
        font-weight: 700;
        font-size: 14px;
        text-transform: uppercase;
        color: #1a2b63;
        margin-bottom: 8px;
    }
    & > p {
        font-weight: 400;
        font-size: 14px;
        color: #1a2b63;
        margin-bottom: 20px !important;
    }
    &__winners {
        display: flex;
        gap: 8px;
        overflow-x: auto;
        padding-bottom: 10px;
    }
    &__winner {
        display: flex;
        justify-content: center;
        border-radius: 8px;
        padding: 4px 8px;
        min-width: 120px;
        background: #fff;
        gap: 16px;
    }
    &__player {
        h3 {
            font-weight: 700;
            font-size: 8px;
            background: linear-gradient(0deg, #1a0b4e 0%, #4630ba 100%);
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
        p {
            font-weight: 400;
            font-size: 8px;
            background: linear-gradient(0deg, #1a0b4e 0%, #4630ba 100%);
            background-clip: text;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
    }
    &__prize {
        font-weight: 700;
        font-size: 9px !important;
        text-align: center;
        background: linear-gradient(360deg, #9c00c3 0%, #ff007a 100%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        max-width: 20px;
    }
    &__wheel {
        position: relative;

        & img {
            &:first-child {
                position: relative;
                width: 100%;
                height: 100%;
                transition: all 1000ms ease-in-out;
            }
            &:last-child {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
        }
    }
    .btn {
        border-radius: 8px;
        width: 100%;
        padding: 22px 15px;
        font-size: 16px;
        text-transform: uppercase;
        flex-direction: column;
        gap: 0px;
        margin-bottom: 12px;
        span {
            display: block;
            font-weight: 400;
            font-size: 10px;
            text-align: center;
        }
    }
}
.wheel-ad {
    background: linear-gradient(360deg, #9c00c3 0%, #ff007a 100%);
    border-radius: 8px;
    padding: 8px 12px;
    margin-bottom: 20px;
    h3 {
        font-weight: 700;
        font-size: 16px;
        text-align: center;
        margin-bottom: 8px;
        color: #fff;
    }
    &__progress {
        width: 100%;
        height: 16px;
        background: #fff;
        border-radius: 16px;
        padding: 2px;
        position: relative;

        .progress-bar {
            border-radius: 12px;
            background: linear-gradient(360deg, #00900e 0%, #04cc00 100%);
            height: 100%;
        }
        .count {
            position: absolute;
            left: 50%;
            top: 1px;
            bottom: 1px;
            transform: translate(-50%, 0%);
            font-weight: 700;
            font-size: 12px;
            text-align: center;
            color: #1a2b63;
        }
    }
}
