.popup-overlay {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 10;
    inset: 0;
    background: rgba(0, 0, 0, 0.5);
    padding: 5px 15px;
}
.popup-content {
    display: flex;
    flex-direction: column;
    border-radius: 24px;
    background: #f5f8f9;
    max-width: 340px;
    max-height: fit-content;
    overflow-y: auto;
    overflow-x: hidden;
    padding: 30px 15px;
    flex: 1 1 100%;
    position: relative;
    overscroll-behavior: contain;

    & input {
        padding: 15px 10px;
        border-radius: 16px;
        margin-top: 24px;
    }
}
.popup__input-btn {
    display: flex;
    gap: 10px;

    margin-top: 32px;

    input {
        margin-top: 0px;
        flex: 1 1 auto;
    }
    button {
        background: rgb(183, 253, 183);
        border-radius: 10px;
    }
}
.popup-btns {
    display: flex;
    gap: 20px;
    max-width: fit-content;
    margin-left: auto;
    position: absolute;
    top: 10px;
    right: 15px;
}
.popup-close {
    border-radius: 29px;
    background: #fff;
    padding: 6px;
    margin-left: auto;
    margin-bottom: 16px;
    cursor: pointer;
    & svg {
        width: 20px;
        height: 20px;
        & path {
            stroke: #8599b1;
        }
    }
}
.popup-fullscreen {
    border-radius: 29px;
    background: #fff;
    padding: 8px;
    margin-left: auto;
    margin-bottom: 16px;
    cursor: pointer;
    & svg {
        & path {
            stroke: #8599b1;
            fill: #8599b1;
        }
    }
}
.popup-title {
    font-size: 14px;
    margin-bottom: 6px;
    font-weight: 700;
}
.popup-status {
    // margin-top: 24px;
    padding: 10px 15px;
    font-weight: 600;

    &.done {
        color: green;
    }
}

@media only screen and (max-width: 992px) {
    .popup-content {
        max-width: 100%;
        padding-top: 40px;
        .bond {
            padding-top: 40px;
        }
    }
    .popup-close {
        position: static;
    }
    .popup-overlay {
        &.error {
            .popup-content {
                p {
                    font-size: 16px;
                    margin: 0px 32px;
                }
            }
        }
    }
}
@media only screen and (max-width: 992px) {
    .popup-overlay {
        &.error {
            .popup-content {
                p {
                    font-size: 14px;

                    margin: 0px 16px;
                }
            }
        }
    }
}
