.text-header {
    margin-bottom: 20px;
    &__title {
        display: flex;
        gap: 10px;
        margin-bottom: 8px;

        h1 {
            font-weight: 400;
            font-size: 14px;
            color: #1a2b63;
            span {
                font-weight: 700;
            }
        }
    }
    h2 {
        font-weight: 400;
        font-size: 14px;
        color: #1a2b63;
    }
}
